import fetch from 'isomorphic-unfetch'

const TvPost = (props) => (
    <div className='container page'>
        <div>
            <h1>{props.show.name}</h1>
            <p>{props.show.summary.replace(/<[/]?p>/g, '')}</p>
            <img src={props.show.image.medium}/>
        </div>
        <a href='javascript:history.back()' className='btn btn-light'>Go back</a>
    </div>
)

TvPost.getInitialProps = async function (context) {
  const { id } = context.query
  const res = await fetch(`https://api.tvmaze.com/shows/${id}`)
  let show = await res.json()
  show.name === 'Not Found' ? show = {name:"Not Found", summary:'Not Found', image:{medium:''}} : 0;

  console.log(`Fetched show: ${show.name}`)

  return { show }
}

export default TvPost